import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { mapActions } from 'vuex'
import Upload from '../Upload'
export default {
  model: {
    prop: 'visible',
    event: 'closeWindow'
  },
  props: ['visible'],
  data () {
    return {
      bpTimeIndex: -1,
      bpThemeIndex: -1,
      swiperThemeOption: {
        slidesPerColumn: 2,
        slidesPerView: 4,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      },
      base64Img: ''
    }
  },
  methods: {
    ...mapActions('app', {
      ChangeBuyDialogInfo: 'ChangeBuyDialogInfo'
    }),
    closeWindow () {
      this.$emit('closeWindow', false)
    },
    afterClip (base64) {
      this.base64Img = base64
    },
    buy () {
      this.ChangeBuyDialogInfo({price: Math.floor(Math.random() * 100), rest: 30})
      this.$emit('onBuy')
    },
    reset () {
      this.bpThemeIndex = -1
      this.bpTimeIndex = -1
    }
  },
  components: {
    Upload,
    swiper,
    swiperSlide
  }

}



