export default {
  model: {
    prop: 'visible',
    event: 'close'
  },
  props: ['visible', 'peoples'],
  methods: {
    close () {
      this.$emit('close', false)
    },
    showCard () {
      this.$emit('onShowCard')
    }
  }
}



